
import React, { FC } from 'react';
import type { FormResetPasswordProps } from './types';
import { Input } from '../../../shared/ui';
import styles from './form-reset-password.module.css';
import { useFormContext } from 'react-hook-form';
import { Button } from '@gravity-ui/uikit';
import { clsx } from 'clsx';

export const FormFieldsResetPassword: FC<FormResetPasswordProps> = ({
	serverErrorText,
}) => {

	const {
		formState: { isValid, errors },
	} = useFormContext();

	return (
    <div className={styles.section}>
        <h1 className={clsx(styles.title, 'text-style-heading-1')}> </h1>
        <p className={clsx(styles.subtitle, 'text-style-body-l')}>  Email,         </p>
        <div className={styles.container}>
            <div className={styles.input_list}>
                <Input
						name="email"
						labelName="E-mail"
						error={errors.email ? `${errors.email?.message}` : ''}
					/>
            </div>


            <Button
					className={styles.button}
					type='submit'
					size='xl'
					view='normal'
					width='max'
					disabled={!isValid}
				>      </Button>
            <span className={styles.server_error}>{serverErrorText}</span>
        </div>
    </div>
	);
};