import * as yup from 'yup';

const schema = yup.object().shape({
	newPassword: yup
		.string()
		.required('   ')
		.min(8, '   8  20 ')
		.max(20, '   8  20 ')
		.matches(
			/[a-zA-Z--0-9!"#$%&'()*+,\-./:;<=>?@[\\\]^_`{|}~\u2116\u0024\u20AC\u00A3\u00A5\u20BD\u00A9\u2122\u00AE]$/,
			'  '
		),

	repeatNewPassword: yup
		.string()
		.required('   ')
		.min(8, '  ')
		.max(20, '  ')
		.matches(
			/[a-zA-Z--0-9!"#$%&'()*+,\-./:;<=>?@[\\\]^_`{|}~\u2116\u0024\u20AC\u00A3\u00A5\u20BD\u00A9\u2122\u00AE]$/,
			'  '
		)
		.oneOf([yup.ref('newPassword')], '    '),
});
export default schema;