import React, { useState } from 'react';
import style from './style.module.css';
import { Button } from '@gravity-ui/uikit';
import media from '../../../../assets/images/Media.png';
import analytics from '../../../../assets/images/Analytics.png';

const slides = [
  {
    title: ' ',
    desc: '       -         ,   35 .',
    img: media,
    buttonText: '',
    buttonAction: 'next'
  },
  {
    title: '',
    desc: '     -,    .     ,   ,     .            .',
    img: analytics,
    buttonText: '',
    buttonAction: 'back'
  }
];

const Slider = () => {
  const [count, setCount] = useState<number>(0);

  const handleButtonClick = (action: string) => {
    setCount(action === 'next' ? count + 1 : count - 1);
  };

  const currentSlide = slides[count];

  return (
      <div className={style.main}>
          <div className={style.info}>
              <p>{count + 1} of {slides.length}</p>
              <div>
                  <h2 className={style.title}>{currentSlide.title}</h2>
                  <p className={style.desc}>{currentSlide.desc}</p>
              </div>
              <Button 
                 size='l' 
                 className={style.button} 
                 onClick={() => handleButtonClick(currentSlide.buttonAction)}
              >
                  {currentSlide.buttonText}
              </Button>
          </div>
          <div className={style.image_block}>
              <img src={currentSlide.img} className={count === 1 ? style.image : ''} />
              {count === 1 && (
                  <Button size='xl' className={style.button_image}> </Button>
              )}
          </div>
      </div>
  );
};

export default Slider;